export default "/*========== CLASS CSS ==========*/\n.section {\n  padding: 2rem 0 1rem;\n}\nion-card {\n  --background: transparent;\n  box-shadow: none;\n}\n.section-title, .section-title-center {\n  font-size: var(--h2-font-size);\n  color: var(--title-color);\n  text-align: center;\n  margin-bottom: var(--mb-3);\n}\n/*========== LAYOUT ==========*/\n.l-main {\n  --margin-top: var(--header-height);\n  overflow: hidden;\n  --background: var(--body-color);\n  --color:var(--text-color);\n  font-family: var(--body-font);\n  font-size: var(--normal-font-size);\n}\n.bd-container {\n  max-width: 968px;\n  width: calc(100% - 3rem);\n  margin-left: var(--mb-3);\n  margin-right: var(--mb-3);\n}\n.bd-grid {\n  display: grid;\n  gap: 1.5rem;\n}\n.l-header {\n  width: 100%;\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: var(--z-fixed);\n  background-color: var(--body-color);\n}\n/*========== NAV ==========*/\n.nav {\n  height: var(--header-height);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n@media screen and (max-width: 768px) {\n  .nav__menu {\n    position: fixed;\n    top: -100%;\n    /* top: var(--header-height); */\n    left: 0;\n    right: 0;\n    width: 90%;\n    margin: 0 auto;\n    padding: 2.5rem 0 0;\n    text-align: center;\n    background-color: var(--body-color);\n    transition: 0.4s;\n    box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);\n    border-radius: 2rem;\n    z-index: var(--z-fixed);\n  }\n}\n.nav__item {\n  margin-bottom: var(--mb-3);\n}\n.nav__link, .nav__logo, .nav__toggle {\n  color: var(--title-color);\n  font-weight: var(--font-semi-bold);\n}\n.nav__logo:hover {\n  color: var(--first-color);\n}\n.nav__link {\n  transition: 0.3s;\n}\n.nav__link:hover {\n  color: var(--first-color);\n}\n.nav__toggle {\n  font-size: 1.3rem;\n  cursor: pointer;\n}\n/* Show menu */\n.show-menu {\n  top: calc(var(--header-height) + 1rem);\n}\n/* Active menu link */\n.active-link {\n  position: relative;\n}\n.active-link::before {\n  content: \"\";\n  position: absolute;\n  bottom: -0.75rem;\n  left: 45%;\n  width: 5px;\n  height: 5px;\n  background-color: var(--title-color);\n  border-radius: 50%;\n}\n/* Scroll top */\n.scrolltop {\n  position: fixed;\n  right: 1rem;\n  /*  bottom: -20%; */\n  bottom: 1.5rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0.3rem;\n  background: rgba(218, 37, 53, 0.5);\n  border-radius: 0.4rem;\n  z-index: var(--z-tooltip);\n  transition: 0.4s;\n  visibility: hidden;\n}\n.scrolltop:hover {\n  background-color: var(--first-color);\n}\n.scrolltop__icon {\n  font-size: 1.5rem;\n  color: var(--body-color);\n}\n.show-scroll {\n  visibility: visible;\n  bottom: 1.5rem;\n}\n/*========== HOME ==========*/\n.home {\n  margin-top: var(--header-height);\n}\n.home__container {\n  row-gap: 0.5rem;\n}\n.home__img {\n  width: 280px;\n  justify-self: center;\n}\n.home__title {\n  font-size: var(--biggest-font-size);\n  font-weight: var(--font-bold);\n  margin-bottom: var(--mb-2);\n}\n.home__description {\n  margin-bottom: var(--mb-3);\n}\n/*========== BUTTONS ==========*/\n.button {\n  display: inline-block;\n  background-color: var(--first-color);\n  color: #fff;\n  padding: 1rem 1.5rem;\n  border-radius: 0.5rem;\n  font-weight: var(--font-semi-bold);\n  transition: 0.3s;\n}\n.button:hover {\n  background-color: var(--first-color-alt);\n}\n.button-link {\n  background: none;\n  padding: 0;\n  color: var(--first-color);\n}\n.button-link:hover {\n  background-color: transparent;\n  color: var(--first-color-alt);\n}\n/*========== SHARE ==========*/\n.share__data {\n  text-align: center;\n}\n.share__description {\n  margin-bottom: var(--mb-2);\n}\n.share__img {\n  width: 280px;\n  justify-self: center;\n}\n/*========== DECORATION ==========*/\n.decoration__container {\n  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));\n}\n/* .decoration__data{\n  text-align: center;\n  padding: 1rem 1rem 2rem;\n  background-color: var(--container-color);\n  box-shadow: 0 2px 6px rgba(65,11,16,.15);\n  border-radius: 1rem;\n} */\n.decoration__data {\n  padding: 0rem 0rem 2rem;\n  border-radius: 1rem;\n}\n.regalos .ct {\n  display: flex;\n  align-items: stretch;\n  flex-wrap: nowrap;\n  justify-content: center;\n  padding: 20px 0;\n}\n.regalos .ct img {\n  border-radius: 100%;\n  height: 160px;\n  width: 160px;\n}\n.regalos .ct .detail {\n  text-align: right;\n  width: 50%;\n  place-self: center;\n}\n.regalos .ct .detail .pd1 {\n  font-weight: 700;\n  font-size: 15px;\n}\n.regalos .ct .detail .pd2 {\n  color: var(--title-color);\n  font-weight: 700;\n  font-size: 17px;\n}\n.regalos .ct .detail .price {\n  font-weight: 600;\n}\n.regalos .ct .detail .price .pnum {\n  font-size: 18px;\n  color: #877363;\n  font-weight: 700;\n}\n.textLeft {\n  text-align: left !important;\n}\n.decoration__data:hover {\n  /* box-shadow: 0 3px 12px rgba(65,11,16,.15); */\n}\n.decoration__img {\n  width: 180px;\n}\n.decoration__title {\n  font-size: var(--h3-font-size);\n  margin-bottom: var(--mb-1);\n}\n/*========== ACCESSORIES ==========*/\n.accessory__container {\n  grid-template-columns: repeat(2, 1fr);\n  padding-bottom: 2rem;\n}\n.accessory__content {\n  position: relative;\n  display: grid;\n  padding: 0.25rem 0.75rem 0.75rem;\n  background-color: var(--container-color);\n  box-shadow: 0 2px 6px rgba(65, 11, 16, 0.15);\n  border-radius: 1rem;\n}\n.accessory__content:hover {\n  box-shadow: 0 3px 12px rgba(65, 11, 16, 0.15);\n}\n.accessory__img {\n  width: 110px;\n  max-height: 140px;\n  -o-object-fit: cover;\n     object-fit: cover;\n  border-radius: 8px;\n  justify-self: center;\n  margin-bottom: 0.25rem;\n}\n.accessory__title, .accessory__category {\n  text-align: center;\n}\n.accessory__title {\n  font-size: var(--normal-font-size);\n}\n.accessory__category {\n  font-size: var(--small-font-size);\n  margin-bottom: var(--mb-1);\n  color: var(--text-color-light);\n}\n.accessory__preci {\n  font-weight: var(--font-semi-bold);\n  color: var(--title-color);\n}\n.accessory__button {\n  position: absolute;\n  bottom: 0;\n  right: 0;\n  display: flex;\n  font-size: 1.25rem;\n  padding: 0.5rem 0.625rem;\n  border-radius: 1rem 0 1rem 0;\n}\n/*========== SEND GIFT ==========*/\n.send {\n  background-color: var(--first-color-send);\n}\n.send__title, .send__description {\n  color: #fff;\n}\n.send__description {\n  text-align: center;\n  margin-bottom: var(--mb-4);\n}\n.send__direction {\n  display: flex;\n  justify-content: space-between;\n  background-color: #fff;\n  padding: 0.5rem;\n  border-radius: 0.5rem;\n}\n.send__input {\n  width: 70%;\n  outline: none;\n  border: none;\n  font-size: var(--normal-font-size);\n  font-family: var(--body-font);\n}\n.send__input::-moz-placeholder {\n  font-family: var(--body-font);\n}\n.send__input::placeholder {\n  font-family: var(--body-font);\n}\n.send__img {\n  width: 280px;\n  justify-self: center;\n}\n/*========== FOOTER ==========*/\n.footer__container {\n  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));\n}\n.footer__logo {\n  color: var(--title-color);\n}\n.footer__title {\n  margin-bottom: var(--mb-2);\n}\n.footer__logo, .footer__title {\n  font-size: var(--h3-font-size);\n}\n.footer__link {\n  display: inline-block;\n  margin-bottom: 0.75rem;\n  color: var(--text-color);\n}\n.footer__link:hover {\n  color: var(--first-color);\n}\n.footer__social {\n  font-size: 1.5rem;\n  color: var(--title-color);\n  margin-right: var(--mb-3);\n}\n.footer__social:hover {\n  color: var(--first-color);\n}\n.footer__copy {\n  text-align: center;\n  font-size: var(--small-font-size);\n  color: var(--text-color-light);\n  margin-top: 4rem;\n}\n/*========== MEDIA QUERIES ==========*/\n/* For full-screen images on small screens */\n@media screen and (max-width: 359px) {\n  .home__img,\n.share__img,\n.send__img {\n    width: 100%;\n  }\n}\n@media screen and (min-width: 576px) {\n  /*  .home__container, */\n  .share__container,\n.send__container {\n    grid-template-columns: repeat(2, 1fr);\n    align-items: center;\n  }\n\n  .home__container {\n    padding: 5rem 0 0;\n  }\n\n  .home__img {\n    order: 1;\n  }\n\n  .section-title-center,\n.share__data,\n.send__description {\n    text-align: initial;\n  }\n\n  .home__img,\n.share__img,\n.send__img {\n    width: 100%;\n  }\n\n  .share__img {\n    order: -1;\n  }\n}\n@media screen and (min-width: 768px) {\n  body {\n    margin: 0;\n  }\n\n  .section {\n    padding-top: 7rem;\n  }\n\n  .nav {\n    height: calc(var(--header-height) + 1.5rem);\n  }\n\n  .nav__list {\n    display: flex;\n    align-items: center;\n  }\n\n  .nav__item {\n    margin-left: var(--mb-5);\n    margin-bottom: 0;\n  }\n\n  .nav__toggle {\n    display: none;\n  }\n\n  .change-theme {\n    position: initial;\n    margin-left: var(--mb-4);\n  }\n\n  .home__container {\n    padding: 7rem 2rem 0;\n  }\n\n  .share__container {\n    padding: 0 2rem;\n  }\n\n  .accessory__container {\n    grid-template-columns: repeat(3, 224px);\n    justify-content: center;\n  }\n\n  .accessory__content {\n    padding: 0.5rem 1.5rem 1.5rem;\n  }\n\n  .accessory__img {\n    width: 120px;\n    margin-bottom: var(--mb-1);\n  }\n\n  .accessory__title, .accessory__category {\n    text-align: initial;\n  }\n\n  .accessory__button {\n    padding: 0.75rem;\n  }\n\n  .send {\n    background: none;\n  }\n\n  .send__container {\n    background-color: var(--first-color-send);\n    padding: 2rem;\n    border-radius: 1.5rem;\n  }\n}\n@media screen and (min-width: 968px) {\n  .bd-container {\n    margin-left: auto;\n    margin-right: auto;\n  }\n\n  .home__img,\n.share__img,\n.send__img {\n    width: 469px;\n  }\n\n  .home__container,\n.share__container,\n.send__container {\n    -moz-column-gap: 5rem;\n         column-gap: 5rem;\n  }\n}\n.animated {\n  transition: 0.5s ease-in-out;\n}\n.class_animateTop {\n  opacity: 0;\n  transform: translate3d(-20px, 0, 0);\n}\n.class_animateLeft {\n  transform: translate3d(0, -10px, 0);\n}\n/*# sourceMappingU