import { IonContent, ModalController } from '@ionic/angular';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ReservarComponent } from '../components/reservar/reservar.component';
import { IonicComponentsService } from 'src/app/services/ionic-components.service';
import { ProductService } from '../services/product.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss']
})

export class HomePage implements OnInit {

  @ViewChild(IonContent, { static: false }) content: IonContent;

  constructor(
    public _comp: IonicComponentsService,
    private _product: ProductService,
    private _modal: ModalController) { }


  listA = [];
  listB = [];

  ngOnInit() {
    this.funcionesJs();
    this.getList();
  }

  isVarInvalid(data: any) {
    if (typeof data === 'string' || data instanceof String) data = data.trim();
    if (data == undefined || data == null || data == 'undefined' || data == 'null' || data == '') return true;
    return false;
  }

  async getList() {
    try {
      const res_list: any = await this._product.getList();
      console.log('res_list',res_list);
      

     /*  if (this.isVarInvalid(res_list.valid)) {
        return;
      } */

      /* res_list.res= res_list.res.filter(a => a.img !=''); */

   /*    this.listA = res_list.res.filter(a => a.status == 1);

      this.listB = res_list.res.filter(a => a.status == 2);
      let listOrder = this.sortJSON(this.listB, 'tmp_reserva', false);
      this.listB = listOrder.filter(item => true);//REFRESCAR TABLA  */

      this.listA = res_list.filter(a => a.status == 1);

      this.listB = res_list.filter(a => a.status == 2);
      let listOrder = this.sortJSON(this.listB, 'tmp_reserva', false);
      this.listB = listOrder.filter(item => true);//REFRESCAR TABLA 

      console.log('this.listA', this.listA);

    } catch (error) {
      console.log('ERROR');
    }
  }

  sortJSON(arr, key, way) {
    return arr.sort(function (a, b) {
      var x = a[key];
      var y = b[key];
      if (typeof x === 'string' || x instanceof String) x = x.toLowerCase();
      if (typeof y === 'string' || y instanceof String) y = y.toLowerCase();

      if (way) { return ((x < y) ? -1 : ((x > y) ? 1 : 0)); }
      if (!way) { return ((x > y) ? -1 : ((x < y) ? 1 : 0)); }
    });
  }

  scrollToLabel(label) {
    var titleELe = document.getElementById(label);
    this.content.scrollToPoint(0, titleELe.offsetTop, 1000);
  }

  async reservar(product) {
    console.log('product', product);
    const modal = await this._modal.create({
      backdropDismiss: false,
      cssClass: 'style-info',
      component: ReservarComponent,
      componentProps: {
        product
      }
    });

    modal.onDidDismiss().then(async (res: any) => {
      console.log(res.data);
      if (this.isVarInvalid(res.data)) return;
      if (!this.isVarInvalid(res.data.updateList)) this.getList();
    });
    return await modal.present();
  }


  copyText(item) {
/*     this.clipboard.copy(this.dataQR); */
    document.addEventListener('copy', (e: ClipboardEvent) => {
      e.clipboardData.setData('text/plain', (item));
      e.preventDefault();
      document.removeEventListener('copy', null);
    });
    document.execCommand('copy');

    this._comp.presentToast('Direcci칩n copiada', 'primary', 500);
  }


  funcionesJs() {
    /*==================== SHOW MENU ====================*/
    const showMenu = (toggleId, navId) => {
      const toggle = document.getElementById(toggleId),
        nav = document.getElementById(navId)

      // Validate that variables exist
      if (toggle && nav) {
        toggle.addEventListener('click', () => {
          // We add the show-menu class to the div tag with the nav__menu class
          nav.classList.toggle('show-menu')
        })
      }
    }
    showMenu('nav-toggle', 'nav-menu')

    /*==================== REMOVE MENU MOBILE ====================*/
    const navLink = document.querySelectorAll('.nav__link')

    function linkAction() {
      const navMenu = document.getElementById('nav-menu')
      // When we click on each nav__link, we remove the show-menu class
      navMenu.classList.remove('show-menu')
    }
    navLink.forEach(n => n.addEventListener('click', linkAction))



    /*==================== SCROLL SECTIONS ACTIVE LINK ====================*/
    const sections = document.querySelectorAll('section[id]')

    function scrollActive() {
      const scrollY = window.pageYOffset

      sections.forEach((current: any) => {
        const sectionHeight = current.offsetHeight
        const sectionTop = current.offsetTop - 50;
        const sectionId = current.getAttribute('id')

        if (scrollY > sectionTop && scrollY <= sectionTop + sectionHeight) {
          document.querySelector('.nav__menu a[href*=' + sectionId + ']').classList.add('active-link')
        } else {
          document.querySelector('.nav__menu a[href*=' + sectionId + ']').classList.remove('active-link')
        }
      })
    }
    window.addEventListener('scroll', scrollActive)


    /*==================== CHANGE BACKGROUND HEADER ====================*/
    function scrollHeader() {
      const nav = document.getElementById('header')
      // When the scroll is greater than 200 viewport height, add the scroll-header class to the header tag
      if (this.scrollY >= 200) nav.classList.add('scroll-header'); else nav.classList.remove('scroll-header')
    }
    window.addEventListener('scroll', scrollHeader)


    /*==================== SHOW SCROLL TOP ====================*/
    function scrollTop() {
      const scrollTop = document.getElementById('scroll-top');
      // When the scroll is higher than 560 viewport height, add the show-scroll class to the a tag with the scroll-top class
      if (this.scrollY >= 560) scrollTop.classList.add('show-scroll'); else scrollTop.classList.remove('show-scroll')
    }
    window.addEventListener('scroll', scrollTop)



    /*==================== DARK LIGHT THEME ====================*/
    const themeButton = document.getElementById('theme-button')
    const darkTheme = 'dark-theme'
    const iconTheme = 'bx-toggle-right'

    // Previously selected topic (if user selected)
    const selectedTheme = localStorage.getItem('selected-theme')
    const selectedIcon = localStorage.getItem('selected-icon')

    // We obtain the current theme that the interface has by validating the dark-theme class
    const getCurrentTheme = () => document.body.classList.contains(darkTheme) ? 'dark' : 'light'
    const getCurrentIcon = () => themeButton.classList.contains(iconTheme) ? 'bx-toggle-left' : 'bx-toggle-right'

    // We validate if the user previously chose a topic
    if (selectedTheme) {
      // If the validation is fulfilled, we ask what the issue was to know if we activated or deactivated the dark
      document.body.classList[selectedTheme === 'dark' ? 'add' : 'remove'](darkTheme)
      themeButton.classList[selectedIcon === 'bx-toggle-left' ? 'add' : 'remove'](iconTheme)
    }

    // Activate / deactivate the theme manually with the button
    themeButton.addEventListener('click', () => {
      // Add or remove the dark / icon theme
      document.body.classList.toggle(darkTheme)
      themeButton.classList.toggle(iconTheme)
      // We save the theme and the current icon that the user chose
      localStorage.setItem('selected-theme', getCurrentTheme())
      localStorage.setItem('selected-icon', getCurrentIcon())
    })

  }

}

